import { FaSave, FaTrash } from "react-icons/fa";
import { Button, Card } from "semantic-ui-react";
import HeaderPage from "../../../components/Layout/HeaderPage";
import { Link, useParams } from 'react-router-dom';
import FormProducto from '../../../components/Productos/FormProducto';
import { useEffect, useState } from "react";
import { getDataFichaProducto } from "../../../actions/productosActions";
import { Loading } from "notiflix";
import { useRef } from "react";

const Ficha = () => {

  const [categorias, setCategorias] = useState([]);
  const [marcas, setMarcas] = useState([]);
  const [producto, setProducto] = useState({});
  const formRef = useRef(null);
  const params = useParams();

  const handleForm = () => {
    formRef.current.handleSubmit();
  }

  useEffect(() => {
    (async () => {
      
      Loading.pulse();
      const { id } = params;

      if(id) {
        const res = await getDataFichaProducto(id);
        
        if(res) {
          setProducto(res.producto);
          setCategorias(res.categorias);
          setMarcas(res.marcas);
        }

      } else {
        
        const res = await getDataFichaProducto();
        
        if(res) {
          setMarcas(res.marcas);
          setCategorias(res.categorias);
        }
      }
      
      Loading.remove();
      
    })()
  }, [])

  return ( 
    <div className="Fichas">
      <HeaderPage>
        <div className="item ui colhidden">
          <Button onClick={handleForm} primary className="ui button">
            <FaSave /> &nbsp;Guardar
          </Button>
        </div>
        <div className="item ui colhidden">
          <Link to="/productos" className="ui button">
            <FaTrash /> &nbsp;Cancelar
          </Link>
        </div>
      </HeaderPage>
      <div className="Ficha__body app__container_body">
        <div className="app_content__padded_view">
          <Card fluid>
            <Card.Content header='Datos del producto' />
            <Card.Content>
              <FormProducto 
                categorias={categorias}
                formRef={formRef}
                producto={producto}
                marcas={marcas}
              />
            </Card.Content>
          </Card>
        </div>
      </div>
    </div>
  );
}
 
export default Ficha;