import { Form, Grid, Dropdown, Image, Header, Divider, TextArea, Button, Label } from "semantic-ui-react";
import { useFormik } from "formik";
import * as Yup from "yup";
import {useDropzone} from 'react-dropzone';
import { forwardRef, useCallback } from "react";
import { useHistory} from 'react-router-dom';
import { addProducto, updateProducto } from "../../../actions/productosActions";
import { getSubCategorias } from "../../../actions/categoriasActions";
import './FormProducto.scss';
import { FaCheck, FaTrash } from "react-icons/fa";
import { useImperativeHandle } from "react";
import { FaFileAlt } from 'react-icons/fa'
import { useEffect, useState } from "react";

function DropzoneWithoutClick({ onDrop, accept = '.jpeg,.png' }) {

  const {
    getRootProps,
    getInputProps,
    isDragActive,
    isDragAccept,
    isDragReject
  } = useDropzone({
    onDrop: onDrop,
    accept: accept
  });


  return (
    <div className="container">
      <div {...getRootProps({className: 'dropzone'})}>
        <input {...getInputProps()} />
        {isDragAccept && (<p>Se aceptar谩n todos los archivos</p>)}
        {isDragReject && (<p>Algunos archivos ser谩n rechazados</p>)}
        {!isDragActive && (<p> Suelta algunos archivos aqu铆 ({accept})</p>)}
      </div>
    </div>
  );
}

const FormProducto = forwardRef((props, ref) => {
  
  const { categorias = [], formRef, producto = {} } = props;

  const [subcategorias, setSubCategorias] = useState([]);
  const history = useHistory();

  const formik = useFormik({
    initialValues: initialValues(),
    validationSchema: Yup.object(validationSchema()),
    onSubmit: async (values) => {

      const formProducto = document.getElementById('formProducto');
      const formData = new FormData(formProducto);
      
      formData.append('category_id', (values.category_id).toString())
      formData.append('subcategory_id', (values.subcategory_id).toString())
      // formData.append('brand_id', (values.brand_id).toString())

      if((values.imagesBim).length > 0) {
        (values.imagesBim).forEach(imagen => {
          formData.append('imagesBim[]', imagen)
        })
      }

      if((values.filesBim).length > 0) {
        (values.filesBim).forEach(file => {
          formData.append('filesBim[]', file)
        })
      }

      formData.append('images', JSON.stringify(values.images));
      formData.append('filesProduct', JSON.stringify(values.files));

      let res = null;
      if(values.id) {
        res = await updateProducto(values.id, formData);
      } else {
        res = await addProducto(formData);
      }
      if(res) {
        history.push('/productos');
      }
    },
  });

  const deleteImageFile = (labelState, label, value) => {
    const items = (formik.values[labelState]).filter(item => item[label] !== value);
  
    formik.setFieldValue(labelState, items);
  }

  const onDropImage = useCallback((acceptedFiles) => {
    formik.setFieldValue('imagesBim', [...formik.values.imagesBim, ...acceptedFiles]);
  }, [formik]);

  const onDropFile = useCallback((acceptedFiles) => {
    formik.setFieldValue('filesBim', [...formik.values.filesBim, ...acceptedFiles]);
  }, [formik]);


  const getImage = (fileReader) => {
    const reader = (Object.assign(fileReader, {
      preview: URL.createObjectURL(fileReader)
    }))
    
    return reader.preview;
  }

  const getCategoriasOptions = (rows = []) => {
    let listCategoria = [{ text: 'Seleccione', value:'0', key: 0 }];

    rows.forEach(categoria => {
      listCategoria = [...listCategoria, {
        key: categoria.id,
        text: categoria.name,
        value: categoria.id
      }];
    })

    return listCategoria;
  }

  // const getMarcasOptions = () => {
  //   let listCategoria = [{ text: 'Seleccione', value:'0', key: 0 }];

  //   marcas.forEach(marca => {
  //     listCategoria = [...listCategoria, {
  //       key: marca.id,
  //       text: marca.name,
  //       value: marca.id
  //     }];
  //   })

  //   return listCategoria;
  // }

  useImperativeHandle(ref, () => ({
    handleSubmit() {
      formik.submitForm();
    }
  }))
  
  useEffect(() => {
    if(producto.id) {
      
      formik.setValues({...producto, imagesBim: [],
        filesBim: [],})
    }
    
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [producto.id])

  useEffect(() => {
    (async () => {
      if(formik.values.category_id) {
        const res = await getSubCategorias(formik.values.category_id);

        if(res) {
          setSubCategorias(res.data);
        }

      }
    })()
    
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [formik.values.category_id])

  return (
    <Form className="FormProducto" onSubmit={formik.handleSubmit} id="formProducto" ref={formRef}>
      <Grid>
        <Grid.Column width={16}>
          <Form.Group>
            <Form.Input
              label="Nombre"
              required
              width={5}
              name="name"
              value={formik.values.name}
              onChange={formik.handleChange}
              error={formik.errors.name}
            />
            {/* <Form.Field required={true} width={4}>
              <label>Marca</label>
              <Form.Input 
                control={Dropdown}
                options={getMarcasOptions()}
                name='brand_id'
                search selection
                value={formik.values.brand_id}
                onChange={(_, data) => formik.setFieldValue('brand_id', data.value)}
                error={formik.errors.brand_id}
              />
            </Form.Field> */}
            <Form.Field required={true} width={4}>
              <label>Categoria</label>
              <Form.Input 
                control={Dropdown}
                options={getCategoriasOptions(categorias)}
                name='category_id'
                search selection
                value={formik.values.category_id}
                onChange={(_, data) => formik.setFieldValue('category_id', data.value)}
                error={formik.errors.category_id}
              />
            </Form.Field>
            {
              (subcategorias.length > 0) && (
                <Form.Field width={5}>
                  <label>SubCategoria</label>
                  <Form.Input 
                    control={Dropdown}
                    options={getCategoriasOptions(subcategorias)}
                    name='subcategory_id'
                    search selection
                    value={formik.values.subcategory_id}
                    onChange={(_, data) => formik.setFieldValue('subcategory_id', data.value)}
                    error={formik.errors.subcategory_id}
                  />
                </Form.Field>
              )
            }
          </Form.Group>
          <Header as="h3">Detalles</Header>
          <Divider />
          <Form.Group>
            <Form.Input
              label="Descripci贸n"
              type="text"
              control={TextArea}
              width={16}
              name="description"
              value={formik.values.description}
              onChange={formik.handleChange}
              error={formik.errors.description}
            />
            <Form.Input
              label="Especificaciones t茅cnicas"
              type="text"
              control={TextArea}
              width={16}
              name="technical_specifications"
              value={formik.values.technical_specifications}
              onChange={formik.handleChange}
              error={formik.errors.technical_specifications}
            />
            <Form.Input
              label="Links"
              type="text"
              control={TextArea}
              width={16}
              name="links"
              value={formik.values.links}
              onChange={formik.handleChange}
              error={formik.errors.links}
            />
          </Form.Group>
          <Form.Group>
            <Form.Input
              label="Links"
              type="text"
              control={TextArea}
              width={16}
              name="links"
              value={formik.values.links}
              onChange={formik.handleChange}
              error={formik.errors.links}
            />
            <Form.Input
              label="Informaci贸n relacionada"
              type="text"
              control={TextArea}
              width={16}
              name="related_information"
              value={formik.values.related_information}
              onChange={formik.handleChange}
              error={formik.errors.related_information}
            />
            <Form.Input
              label="Clasificaci贸n"
              type="text"
              control={TextArea}
              width={16}
              name="classification"
              value={formik.values.classification}
              onChange={formik.handleChange}
              error={formik.errors.classification}
            />
          </Form.Group>
          <Form.Group>
            <Form.Input
              label="Propiedades"
              type="text"
              control={TextArea}
              width={6}
              name="properties"
              value={formik.values.properties}
              onChange={formik.handleChange}
              error={formik.errors.properties}
            />
          </Form.Group>
          <Header as="h3">Im谩genes</Header>
          <Divider />
          
          <DropzoneWithoutClick onDrop={onDropImage} accept=".jpeg,.png"/>
          <div className="images-productos" style={{marginBottom: 20}}>
          {(formik.values.imagesBim).map((image, index) => (
            <div className="images-productos__item" key={index}>
              <Image size="small" src={getImage(image)} />
              <div className="images-productos__item__actions">
                <Button type="button" onClick={() => deleteImageFile('imagesBim', 'lastModified', image.lastModified)} size="tiny" color="red" icon><FaTrash /></Button>
              </div>
              
            </div>
          ))}
          
          </div>
          
          <div className="images-productos">
          {(formik.values.images).map((image, index) => (
            <div className="images-productos__item" key={index}>
              <Image size="small" src={`${process.env.REACT_APP_HOST_URL}/${image.path}`} />
              <div className="images-productos__item__actions">
                <Label color="green"><FaCheck /></Label>
                <Button type="button" size="tiny" color="red" onClick={() => deleteImageFile('images', 'id', image.id)} icon><FaTrash /></Button>
              </div>
              
            </div>
          ))}
          </div>
          <Header as="h3">Archivos</Header>
          <Divider />

          <DropzoneWithoutClick onDrop={onDropFile} accept=".pdf,.rtv,.rvt,.doc,.csv,.docx,.xls,.xlsx,.rfa,.rar,.zip,.ifc"/>
          <div className="images-productos" style={{marginBottom: 20}}>
          
            {(formik.values.filesBim).map((file, index) => (
              <div className="images-productos__item" key={index}>
                <div className="file">
                  <FaFileAlt style={{fontSize: 70}} /><br/>
                  <p>{file.name}</p>
                </div>

                <div className="images-productos__item__actions">
                  <Button type="button" size="tiny" color="red" onClick={() => deleteImageFile('filesBim', 'lastModified', file.lastModified)} icon><FaTrash /></Button>
                </div>
                
              </div>
            ))}
          </div>
          <div className="images-productos">
          
            {(formik.values.files).map((file, index) => (
              <div className="images-productos__item" key={index}>
                <div className="file">
                  <a href={`${process.env.REACT_APP_HOST_URL}/${file.path}`} rel="noreferrer" target='_blank'>
                    <FaFileAlt style={{fontSize: 70}} />
                  </a><br/>
                  <p>{file.name}</p>
                </div>

                <div className="images-productos__item__actions">
                  <Label color="green"><FaCheck /></Label>
                  <Button type="button" size="tiny" color="red" onClick={() => deleteImageFile('files', 'id', file.id)} icon><FaTrash /></Button>
                </div>
                
              </div>
            ))}
          </div>
        </Grid.Column>
        {/* <Grid.Column width={4}>
        </Grid.Column> */}
      </Grid>
    </Form>
  );
});

export default FormProducto;

const initialValues = () => {
  return {
    name: "",
    description: "",
    technical_specifications: "",
    links: "",
    related_information: "",
    classification: "",
    properties: "",
    // precio: "",
    category_id: "0",
    subcategory_id: "0",
    // brand_id: "0",
    imagesBim: [],
    filesBim: [],
    images: [],
    files: []
  };
};

const validationSchema = () => {
  const validation = {
    name: Yup.string().required("Este campo es obligatorio"),
    // precio: Yup.number()
    //   .positive("Solo n煤meros positivos")
    //   .required("Este campo es requerido")
    //   .typeError("Solo se permiten n煤meros"),
    category_id: Yup.number().positive('Este campo es requerido').required('Este campo es requerido.'),
    // brand_id: Yup.number().positive('Este campo es requerido').required('Este campo es requerido.')
  };
  return validation;
};
