import { Notify } from "notiflix";
import authAxios from "../config/authAxios";
import { STATUS_OK } from "../config/constants";
import response from "../config/network/response";
import { getSeccionsAsync } from './seccionActions';

export const getCategoriaById = async (id) => {
  
    try {
      const res = await authAxios.get(`/category/${id}`);

      if(res.data.status === STATUS_OK) {
        return (res.data.body);
      } else {
        return null;
      }
    } catch (error) {
      return null;
    }
}

export const getCategoriaByIdAsync = (id) => {
  return new Promise( async (resolve) => {
    try {

      if(!id) {
        resolve(null);
      }

      const res = await authAxios.get(`/category/${id}`);

      if(res.data.status === STATUS_OK) {
        resolve(res.data.body);
      } else {
        resolve(null);
      }

    } catch (error) {
      resolve(null);
    }
  })
}


export const addCategoria = async (datos) => {
  try {
    
    const res = await authAxios.post('/category', datos);

    if(res.data.status === STATUS_OK){
      Notify.success('Se creo correctamente.');
      return true;
    } else {
      if(res.data.msg) {
        response.error(null, res.data.msg);
      } else {
        response.error('Opps... hubo un error inesperador.');
      }
      return false;
    }

  } catch (error) {
    response.error(error);
    return false;
  }
}

export const updateCategoria = async (id, datos) => {
  try {
    
    const res = await authAxios.put(`/category/${id}`, datos);

    if(res.data.status === STATUS_OK){
      Notify.success('Se edit贸 correctamente.');
      return true;
    } else {
      if(res.data.msg) {
        response.error(null, res.data.msg);
      } else {
        response.error('Opps... hubo un error inesperador.');
      }
      return false;
    }

  } catch (error) {
    response.error(error);
    return false;
  }
} 


export const deleteCategoria = async id => {
  try {
    const res = await authAxios.delete(`/category/${id}`);

    if(res.data.status === STATUS_OK) {
      response.success(null, 'Se elimino correctamente.');
      return true;
    }

    return false;

  } catch (error) {
    response.error(error);
    return false;
  }
}


export const getCategorias = async () => {
  try {
    const res = await authAxios.get('/category');
  
    if(res.data.status === STATUS_OK) {
      return res.data.body;
    } else {
      return false;
    }

  } catch (error) {
    response.error(error);
    return false;
  }
}

export const getSubCategorias = async (idParent) => {
  try {
    const res = await authAxios.get(`/category?level=1&parent=${idParent}`);
  
    if(res.data.status === STATUS_OK) {
      return res.data.body;
    } else {
      return false;
    }

  } catch (error) {
    response.error(error);
    return false;
  }
}

export const getCategoriasAsync = async () => {
  
  return new Promise(async (resolve) => {
    try {
      const res = await authAxios.get('/category');
    
      if(res.data.status === STATUS_OK) {
        resolve(res.data.body.data);
      } else {
        resolve(null);
      }
  
    } catch (error) {
      resolve(null);
    }
  })
}

export const getDataFichaCategoria= async (id) => {
  try {
    const res = await Promise.all([getSeccionsAsync(), getCategoriaByIdAsync(id)]);

    return {
      secciones: res[0] ? res[0] : [],
      categoria: res[1] ? res[1] : null
    }

  } catch (error) {
    console.log(error);
    response.error(null, 'Opps... hubo un error inesperado');
    return null;
  }
}
