import { Notify } from "notiflix";
import authAxios from "../config/authAxios";
import { STATUS_OK } from "../config/constants";
import response from "../config/network/response";

export const getMarcaById = async (id) => {
  
    try {
      const res = await authAxios.get(`/brand/${id}`);

      if(res.data.status === STATUS_OK) {
        return (res.data.body);
      } else {
        return null;
      }
    } catch (error) {
      return null;
    }
}


export const addMarca = async (datos) => {
  try {
    
    const res = await authAxios.post('/brand', datos);

    if(res.data.status === STATUS_OK){
      Notify.success('Se creo correctamente.');
      return true;
    } else {
      if(res.data.msg) {
        response.error(null, res.data.msg);
      } else {
        response.error('Opps... hubo un error inesperador.');
      }
      return false;
    }

  } catch (error) {
    response.error(error);
    return false;
  }
}

export const updateMarca = async (id, datos) => {
  try {
    
    const res = await authAxios.put(`/brand/${id}`, datos);

    if(res.data.status === STATUS_OK){
      Notify.success('Se edit贸 correctamente.');
      return true;
    } else {
      if(res.data.msg) {
        response.error(null, res.data.msg);
      } else {
        response.error('Opps... hubo un error inesperador.');
      }
      return false;
    }

  } catch (error) {
    response.error(error);
    return false;
  }
} 


export const deleteMarca = async id => {
  try {
    const res = await authAxios.delete(`/brand/${id}`);

    if(res.data.status === STATUS_OK) {
      response.success(null, 'Se elimino correctamente.');
      return true;
    }

    return false;

  } catch (error) {
    response.error(error);
    return false;
  }
}

export const getMarcas = async () => {
  try {
    const res = await authAxios.get('/brand');
  
    if(res.data.status === STATUS_OK) {
      return res.data.body;
    } else {
      return false;
    }

  } catch (error) {
    response.error(error);
    return false;
  }
}

export const getMarcasAsync = async () => {
  
  return new Promise(async (resolve) => {
    try {
      const res = await authAxios.get('/brand');
    
      if(res.data.status === STATUS_OK) {
        resolve(res.data.body.data);
      } else {
        resolve(false);
      }
  
    } catch (error) {
      resolve(false);
    }
  })
}
