import { Notify } from "notiflix";
import authAxios from "../config/authAxios";
import { STATUS_OK } from "../config/constants";
import response from "../config/network/response";

export const getSeccionById = async (id) => {
  
    try {
      const res = await authAxios.get(`/section/${id}`);

      if(res.data.status === STATUS_OK) {
        return (res.data.body);
      } else {
        return null;
      }
    } catch (error) {
      return null;
    }
}

export const addSeccion = async (datos) => {
  try {
    
    const res = await authAxios.post('/section', datos);

    if(res.data.status === STATUS_OK){
      Notify.success('Se creo correctamente.');
      return true;
    } else {
      if(res.data.msg) {
        response.error(null, res.data.msg);
      } else {
        response.error('Opps... hubo un error inesperador.');
      }
      return false;
    }

  } catch (error) {
    response.error(error);
    return false;
  }
}

export const updateSeccion = async (id, datos) => {
  try {
    
    const res = await authAxios.put(`/section/${id}`, datos);

    if(res.data.status === STATUS_OK){
      Notify.success('Se edit贸 correctamente.');
      return true;
    } else {
      if(res.data.msg) {
        response.error(null, res.data.msg);
      } else {
        response.error('Opps... hubo un error inesperador.');
      }
      return false;
    }

  } catch (error) {
    response.error(error);
    return false;
  }
} 


export const deleteSeccion = async id => {
  try {
    const res = await authAxios.delete(`/section/${id}`);

    if(res.data.status === STATUS_OK) {
      response.success(null, 'Se elimino correctamente.');
      return true;
    }

    return false;

  } catch (error) {
    response.error(error);
    return false;
  }
}

export const getSecciones = async () => {
  try {
    const res = await authAxios.get('/sections');
  
    if(res.data.status === STATUS_OK) {
      return res.data.body;
    } else {
      return false;
    }

  } catch (error) {
    response.error(error);
    return false;
  }
}

export const getSeccionsAsync = async () => {
  
  return new Promise(async (resolve) => {
    try {
      const res = await authAxios.get('/sections');
    
      if(res.data.status === STATUS_OK) {
        resolve(res.data.body.data);
      } else {
        resolve(false);
      }
  
    } catch (error) {
      resolve(false);
    }
  })
}
