import { Notify } from "notiflix";
import authAxios from "../config/authAxios";
import { STATUS_OK } from "../config/constants";
import loadAxios from "../config/network/loadAxios";
import response from "../config/network/response"
import { getCategoriasAsync } from "./categoriasActions";
import { getMarcasAsync } from "./marcasActions";


export const addProducto = async (datosPlato) => {
  try {
    
    const res = await loadAxios({ url: '/product', body: datosPlato, method: 'POST' });

    if(res.data.status === STATUS_OK){
      Notify.success('Se creo nuevo producto correctamente.');
      return true;
    } else {
      if(res.data.message) {
        response.error(null, res.data.message);
      } else {
        response.error('Opps... hubo un error inesperador.');
      }
      return false;
    }

  } catch (error) {
    response.error(error);
    return false;
  }
} 

export const getProductoByIdAsync = (idProducto) => {
  return new Promise( async (resolve) => {
    try {

      if(!idProducto) {
        resolve(null);
      }

      const res = await authAxios.get(`/product/${idProducto}`);

      if(res.data.status === STATUS_OK) {
        resolve(res.data.body);
      } else {
        resolve(null);
      }

    } catch (error) {
      resolve(null);
    }
  })
}

export const getDataFichaProducto = async (idProducto) => {
  
  try {

    const res = await Promise.all([getCategoriasAsync(), getMarcasAsync(), getProductoByIdAsync(idProducto)]);
    
    return {
      producto: res[2] ? res[2] : {},
      marcas: res[1],
      categorias: res[0] 
    }
   
  } catch (error) {
    console.log(error);
    response.error(null, 'Opps... hubo un error inesperado');
    return null;
  }
}

export const getProductosAsync = () => {
  return new Promise(async (resolve) => {
      try {
        const res = await authAxios.get(`/product`);
  
        if(res.data.status === STATUS_OK) {
          resolve(res.data.body);
        } else {
          response.error(null, res.data.message);
          resolve([]);
        }
      } catch (error) {
        response.error(error);
        resolve([]);
      }
  })
}

export const updateProducto = async (idPlato, dataPlato) => {
  try {
    const res = await loadAxios({ url: `/product/${idPlato}`, body: dataPlato, method: 'POST' })

    if(res.data.status === STATUS_OK) {
      Notify.success('Se edit贸 el plato correctamente.');
      return true;
    } else {
      if(res.data.message) {
        response.error(null, res.data.message);
      } else {
        response.error('Opps... hubo un error inesperador.');
      }
    }

  } catch (error) {
    response.error(error);
    return false;
  }
}

export const deleteProducto = async idProducto => {
  try {
    const res = await authAxios.delete(`/product/${idProducto}`);

    if(res.data.status === STATUS_OK) {
      Notify.success('Se elimino el producto correctamente.')
      return true;
    }
    
    if(res.data.message) {
      response.error(null, res.data.message);
    } else {
      response.error('Opps... hubo un error inesperador.');
    }

    return false;

  } catch (error) {
    response.error(error);
  }
}
